//global setup

*,
*::before,
*::after {
	box-sizing: border-box;
}
.list-inline {
	list-style: none;
	padding-left: 0;
	margin-bottom: 0;
	li {
		display: inline-block;
	}
}
.input {
  outline: none;
  border: 1px solid #000;
  border-radius: 4px;
  padding: 5px 10px 5px 30px;
  width: 100%;
}
figure {
	img {
		max-width: 100%;
	}
}
